.main-dashboard-component {
    margin-bottom: 60px;

    h1 {
        color: var(--Text, #211b3a);
        font-family: Urbanist;
        font-size: 24px;
        font-style: normal;
        font-weight: 600;
        line-height: 28px;
        /* 116.667% */
    }
}

.cards-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 16px;

    .stats-card {
        border-radius: 10px;
        background: #fff;
        padding: 16px;

        .card-heading {
            display: flex;
            align-items: center;
            gap: 8px;

            h3 {
                color: var(--Text, #211b3a);
                font-family: Urbanist;
                font-size: 16px;
                font-style: normal;
                font-weight: 500;
                line-height: 24px;
                letter-spacing: 0.15px;
                margin: 0px;
            }
        }
    }
}

.tableheading {
    h5 {
        color: #211b3a;
        font-family: Urbanist;
        font-size: 20px;
        font-style: normal;
        font-weight: 600;
        line-height: 28px;
        /* 140% */
    }

    input.form-control {
        min-width: 300px;
        border-radius: 8px;
        padding: 8px 12px;
        overflow: hidden;
        color: #868686;
        font-family: Urbanist;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 20px;
        letter-spacing: 0.25px;
    }
}

.table th {
    font-family: Urbanist;
    color: #492fb2;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 16px;
    /* 114.286% */
    letter-spacing: 0.07px;
}

.table td {
    color: #333;
    font-family: Urbanist;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    /* 21px */
    letter-spacing: 0.25px;
}

.custom-table {
    border: 1px solid #e5e7eb;
    border-radius: 8px;

    thead th {
        background-color: #eee;
        padding: 12px;
    }
}

.badge {
    border-radius: 8px;
    font-family: "Noto Sans";
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: var(--Line-Heights-10, 20px);
    letter-spacing: var(--Letter-Spacing-4, 0.25px);
    padding: 6px 12px;
}

.Success {
    color: #3C9718;
    background: #EDF7E6;
}

.Failed {
    color: #b72424;
    background: #fee7e7;
}

.dropdown-menu {
    border-radius: 8px;
    border: 1px solid #c6c6c6;
    background: #fff;
    cursor: pointer;
    padding: 8px 0px;

    .dropdown-item {
        padding: 8px 16px;
        display: flex;
        align-items: center;
        gap: 8px;
        font-family: Urbanist;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
    }

    .blue {
        color: #492fb2;
    }

    .black {
        color: #211b3a;
    }

    .red {
        color: #b7131a;
    }
}

.btn-check:checked+.btn,
.btn.active,
.btn.show,
.btn:first-child:active,
:not(.btn-check)+.btn:active {
    border-color: transparent;
}



.offcanvas {
    width: 520px; // width of the drawer
}

.click-here {
    color: var(--gov-in-purple, #290874);
    font-family: Urbanist;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    /* 171.429% */
}

.upload-box {
    border-style: dashed;
    cursor: pointer;
    transition: background 0.3s;
}

.upload-box:hover {
    background: #f8f9fa;
}

.form-label {
    color: var(--Text-Dark, #212121);
    font-family: Urbanist;
    font-size: var(--Font-Size-2, 14px);
    font-style: normal;
    font-weight: 500;
    line-height: var(--Line-Heights-10, 20px);
    /* 142.857% */
    letter-spacing: var(--Letter-Spacing-2, 0.1px);
}

.upload-wrapper {
    .upload-box {
        display: block;
        border: 2px dashed #AFAFAF;
        border-radius: 10px;
        padding: 40px;
        cursor: pointer;
        transition: all 0.3s ease;
        background-color: #f8f9fa;

        &:hover {
            background-color: #e9f3ff;
        }
    }

    .upload-content {
        display: flex;
        align-items: center;
        color: #6c757d;
        justify-content: space-between;
    }

    .preview-box {
        text-align: center;

        img {
            max-width: 200px;
            border-radius: 10px;
            margin-top: 10px;
        }
    }
}

.modern-pagination .page-item {
    margin: 0 3px;
}

.modern-pagination .page-link {
    border-radius: 8px !important;
    border: 1px solid #ddd;
    padding: 6px 12px;
    color: #444;
    background-color: #fff;
    transition: all 0.2s ease;
    cursor: pointer;
}

.modern-pagination .page-link.active {
    background-color: #f5f0ff;
    /* light purple */
    border: 1px solid #7c3aed;
    /* purple border */
    color: #7c3aed;
    /* purple text */
    font-weight: 600;
}

.modern-pagination .page-link:hover {
    background-color: #f2f2f2;
    color: #000;
}

.modern-pagination .page-item.disabled .page-link {
    opacity: 0.5;
    pointer-events: none;
}

.file-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #dbe3ff;
    padding: 12px 20px;
    border-radius: 12px;
    max-width: 100%;
    font-family: Arial, sans-serif;
}

.file-info {
    display: flex;
    align-items: center;
    gap: 12px;
    color: #0b1e4a;
    font-weight: 500;
}

.radio-icon {
    width: 16px;
    height: 16px;
    border: 2px solid #0b1e4a;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.radio-icon::before {
    content: '';
    width: 8px;
    height: 8px;
    background-color: #0b1e4a;
    border-radius: 50%;
}

.view-icon {
    color: #0b1e4a;
    font-size: 18px;
    cursor: pointer;
}

.modal-backdrop {
    z-index: 1040 !important;
}

.modal {
    z-index: 1055 !important;
}